<template>
  <div class="early-warning-router-container">
    <div class="main-card">
      <div class="main-header">
        <span class="main-title">今日预警</span>
        <div class="close-btn" @click="cancelBtn">×</div>
      </div>
      <div class="main-number">
        <div class="box">
          <span class="number">{{ formData.total }}</span>
          <span class="title-warning">(件)</span>
        </div>
        <div class="box">
          <span class="title-warning">待反馈</span><span class="number">{{ formData.warningNumber }}</span><span class="title-warning">(件)</span>
        </div>
        <div class="box">
          <span class="title-handle">已反馈</span><span class="number">{{ formData.handleNumber }}</span><span class="title-warning">(件)</span>
        </div>
      </div>

      <div class="early-warning-router-body">
        <!-- 左侧 -->
        <div class="body-left">
          <h1>任务基本信息</h1>
          <div class="body-left-text">
            <div class="text-content">
              <span>网格编号： {{data.subgrid_code}}</span>
              <span>任务状态： {{data.status_name}}</span>
            </div>
            <div class="text-content">
              <span>开始时间： {{data.occur_datetime}}</span>
              <span>任务类型： 网格报警</span>
            </div>
            <div class="text-content">
              <span>污染物类型： PM2.5</span>
              <span>周边企业： —</span>
            </div>
            <div class="text-content">
              <span>污染类型：
                <select v-model="value" @click="handelSelect(value)">
                  <option>PM<sub>2.5</sub></option>
                </select>
              </span>
              <!-- <div>
                <select v-model="date1" @click="handelSelect(date1)">
                  <option>2021-12-22</option>
                  <option>2021-12-24</option>
                  <option>2021-12-26</option>
                </select>
                <span class="divider">~</span>
                <select v-model="date2" @click="handelSelect(date2)">
                  <option>2021-12-23</option>
                  <option>2021-12-25</option>
                  <option>2021-12-27</option>
                </select>
                <span class="query">查询</span>
              </div> -->
            </div>
          </div>
          <div ref="echartsid" class="body-left-echarts"></div>
        </div>
        <!-- 右侧 -->
        <div class="body-right">
          <h1>问题反馈与跟进</h1>
          <div class="body-right-text">
            <p>反馈一</p>
            <div class="text-content">
              <span>污染源名称： {{flag ? data.feedback_list[0].pollution_name:''}}</span>
              <span>反馈类型： {{flag? data.feedback_list[0].status_name:''}}</span>
            </div>
            <div class="text-content">
              <span>污染源地址： {{flag? data.feedback_list[0].pollution_name:''}}</span>
              <span>反馈时间： {{flag? data.feedback_list[0].feedback_datetime:''}}</span>
            </div>
            <div class="text-content">
              <span>反馈人员： {{flag? data.feedback_list[0].reporter_name:''}}</span>
              <span>所属单位： ——</span>
            </div>
            <div class="text-content">
              <span>联系方式： {{flag ? data.feedback_list[0].account:''}}</span>
              <span>反馈详情： {{flag? data.feedback_list[0].handle_desc:''}}</span>
            </div>
          </div>
          <div class="body-right-img">
            <!-- <img
              v-if="!flag"
              src="https://fuss10.elemecdn.com/a/3f/3302e58f9a181d2509f3dc0fa68b0jpeg.jpeg"
              alt=""
            /> -->
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import ImgScroll from "./ImgScroll";
import * as echarts from "echarts";
import axios from "axios";
export default {
  components: {
    ImgScroll,
  },
  data() {
    return {
      formData: null,
      id: "",
      wangGeData: [],
      zhouBianData: [],
      eventId:'',
      //   urls: [
      //     "https://fuss10.elemecdn.com/a/3f/3302e58f9a181d2509f3dc0fa68b0jpeg.jpeg",
      //     "https://fuss10.elemecdn.com/1/34/19aa98b1fcb2781c4fba33d850549jpeg.jpeg",
      //     "https://fuss10.elemecdn.com/0/6f/e35ff375812e6b0020b6b4e8f9583jpeg.jpeg",
      //     "https://fuss10.elemecdn.com/9/bb/e27858e973f5d7d3904835f46abbdjpeg.jpeg",
      //     "https://fuss10.elemecdn.com/d/e6/c4d93a3805b3ce3f323f7974e6f78jpeg.jpeg",
      //     "https://fuss10.elemecdn.com/3/28/bbf893f792f03a54408b3b7a7ebf0jpeg.jpeg",
      //     "https://fuss10.elemecdn.com/2/11/6535bcfb26e4c79b48ddde44f4b6fjpeg.jpeg",
      //   ],
      data: {},
      flag: false,
      value: "PM2.5",
      date1: "2021-12-22",
      date2: "2021-12-23",
      option: {
        grid: {
          width: "87%",
          left: "80",
        },
        title: {
          text: `μg/m³`,
          textStyle: {
            color: "#fff",
          },
          left: "50px",
          top: "-5px",
        },
        color: ["red", "green"],
        legend: {
          show: true,
          textStyle: {
            color: "#C8D6E2",
            fontSize: "25px",
          },
          top: "15",
        },
        tooltip: {
          show: true,
          trigger: "axis",
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.dateArr,
          axisLabel: {
            textStyle: {
              color: "#C8D6E2",
              fontSize: "19px",
            },
            lineHeight: "80",
          },
          axisTick: {
            show: true,
            length: 8,
            lineStyle: {
              color: "#C8D6E2",
            },
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            textStyle: {
              color: "#C8D6E2",
              fontSize: "19px",
            },
            padding: [0, 10, 0, 0],
          },
          splitLine: {
            show: false,
          },
          axisLine: {
            show: true,
          },
          axisTick: {
            show: true,
            length: 9,
            lineStyle: {
              color: "#C8D6E2",
            },
          },
        },
        series: [
          {
            name: "网格浓度",
            data: this.wangGeData,
            type: "line",
            smooth: true,
          },
          {
            name: "周边浓度",
            data: this.zhouBianData,
            type: "line",
            smooth: true,
          },
        ],
      },
    };
  },
  methods: {
    cancelBtn() {
      this.$router.push({ name: "Home" });
    },
    handelSelect(e) {
      console.log("223 e", e);
    },
    initCharts() {
      this.myChart = echarts.init(this.$refs.echartsid);
      this.myChart.setOption(this.option, false);
    },
    getPoliceData() {
      let json = {
        token:
          "qiYrxXEv5mJPW6-dU5_wQdLtC1_mif6WXzKWnsPjoffaDlrgUxAkVwpEpJ3ZjcBf4JbJ9HzT_pVp_SzBR-6ubey5gp_nLFpGMCJVG87_UhJmNLq547Sr4awrLmR91rrCDayhWWTehaS0_CgvQFFzaePlXI2R4am8jv9TNg_z1Qs6XO7jICeRjFU67jZ_yEjfi29LCWhhgQGlI-fCZg2dDZSQ5tbRFDRb7BVu1dCuEgU=",
        event_id: this.id,
      };
      axios
        .get(
          "https://api-daily-supervision-pre.airqualitychina.cn:9998/api/v2/event/details",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          this.data = res.data.result;
          this.data.feedback_list.length > 0
            ? (this.flag = true)
            : (this.flag = false);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 获取周边 和网格密度
     getMiDuData() {
      let json = {
          "username": "api", 
          "password": "a123456",
        event_id: this.eventId,
      };
      axios
        .get(
          "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/eventdatas",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          this.dateArr = res.data.result[0].data_datetime
          this.wangGeData = res.data.result[0].est_value
           this.wangGeData =  this.wangGeData.map(item=>{
                return item.toFixed(2)
            })
          this.zhouBianData = res.data.result[0].nearby_est_value
          this.zhouBianData = this.zhouBianData.map(item=>{
                return item.toFixed(2)
            })
          this.option = {
        grid: {
          width: "87%",
          left: "80",
        },
        title: {
          text: `μg/m³`,
          textStyle: {
            color: "#fff",
          },
          left: "50px",
          top: "-5px",
        },
        color: ["red", "green"],
        legend: {
          show: true,
          textStyle: {
            color: "#C8D6E2",
            fontSize: 18,
          },
          top: "15",
        },
        tooltip: {
          show: true,
          trigger: "axis",
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.dateArr,
          axisLabel: {
            textStyle: {
              color: "#C8D6E2",
              fontSize: "19px",
            },
            lineHeight: "80",
          },
          axisTick: {
            show: true,
            length: 8,
            lineStyle: {
              color: "#C8D6E2",
            },
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            textStyle: {
              color: "#C8D6E2",
              fontSize: "19px",
            },
            padding: [0, 10, 0, 0],
          },
          splitLine: {
            show: false,
          },
          axisLine: {
            show: true,
          },
          axisTick: {
            show: true,
            length: 9,
            lineStyle: {
              color: "#C8D6E2",
            },
          },
        },
        series: [
          {
            name: "网格浓度",
            data: this.wangGeData,
            type: "line",
            smooth: true,
            
          },
          {
            name: "周边浓度",
            data: this.zhouBianData,
            type: "line",
            smooth: true,
          },
        ],
      },
          this.initCharts();
        })
        
    },
  },
  created() {
    this.formData = this.$route.query.details;
    this.id = this.$route.query.details.item.id;
    this.eventId = this.$route.query.details.item.source[0].event_id
    console.log(this.$route.query.details.item.source[0].event_id)
  },
  mounted() {
    
    this.getPoliceData();
    this.getMiDuData()
  },
  updated() {
    // 初始化图表
    this.initCharts();
  },
  watch: {
    $route(newVla) {
      this.formData = newVla.query.details;
    },
  },
};
</script>

<style lang="scss" >
