      }
    }
    .body-right {
      width: 1016px;
      height: 628px;
      background: url("../../assets/right-bg.png") 0 no-repeat;
      background-size: 100% 100%;
      margin-left: 30px;
      padding: 25px;
      .body-right-text {
        width: 100%;
