            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
      .body-right-img {
        width: 100%;
        height: 42%;
        img {
          width: 50%;
          height: 100%;
        }
      }
    }
    h1 {
      font-size: 50px;
    }
