<template>
    <div class="img-scroll-container">
        <div class="img-title">{{ imgTitle }}</div>
        <div class="img-box">
            <vue-seamless-scroll :data="urls" class="seamless-warp"  :class-option="classOption">
                <img v-for="url in urls" :key="url" :src="url" alt="">
            </vue-seamless-scroll>
        </div>
    </div>
</template>

<script>
export default {
    props: ["imgTitle", "urls"],
    mounted(){
    },
    computed:{
        classOption() {
            return {
            step: .5, // 数值越大速度滚动越快
            // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
            hoverStop: true, // 是否开启鼠标悬停stop
            direction: 2, // 0向下 1向上 2向左 3向右
            // openWatch: true, // 开启数据实时监控刷新dom
            singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
            singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
            waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
            };
        },
    }
};
</script>

<style lang="scss" scoped>
.img-scroll-container {
    margin-bottom: 85px;

    .img-title {
        line-height: 39px;
        text-align: left;
        font-size: 28px;
        color: #74f4f7;
        margin-bottom: 42px;
    }
    .img-box {
        width: 1017px;
        height: 175px;
        overflow: hidden;
        img {
            width: 260px;
            height: 175px;
            margin-right: 15px;
        }
    }
}
</style>